<template>
  <div class="converter">
    <h1>String to Hex</h1>
    <v-text-field clearable autofocus v-model="v_string" :counter="500" label="String" data-vv-name="v_string" required></v-text-field>
    <v-text-field clearable v-model="v_hex" :counter="500" label="Hex" data-vv-name="v_hex" required></v-text-field>
  </div>
</template>

<script>
import Bignumber from "bignumber.js";
import Web3 from "web3";

export default {
  props: {
    source: String
  },
  data: () => ({
    v: ""
  }),
  computed: {
    v_string: {
      get: function() {
        return this.v;
      },
      set: function(newValue) {
        this.v = newValue
      }
    },
    v_hex: {
      get: function() {
        const _hex = Web3.utils.toHex(this.v);
        return _hex;
      },
      set: function(newValue) {
        try {
          const _uft8 = Web3.utils.hexToUtf8(newValue);
          this.v = _uft8
        } catch (error) {
          console.log(error)
        }
      },
    },
  },
  methods: {
    enter(e) {

    }
  },
  created() {}
};
</script>

