  <div class="converter">
    <h1>String to Bytes32</h1>
    <v-text-field v-model="v_string" :counter="32" label="String" data-vv-name="v_string" required></v-text-field>
    <v-text-field v-model="v_bytes32" :counter="66" label="Bytes32" data-vv-name="v_bytes32" required></v-text-field>
  </div>
</template>

<script>
import Bignumber from "bignumber.js";
import Web3 from "web3";

export default {
  props: {
    source: String
  },
  data: () => ({
    v: ""
  }),
  computed: {
    v_string: {
      get: function() {
        return this.v;
      },
      set: function(newValue) {
        this.v = newValue
      }
    },
    v_bytes32: {
      get: function() {
        const _hex = Web3.utils.utf8ToHex(this.v);
        const _hexPadRight = Web3.utils.padRight(_hex, 64)
        return _hexPadRight;
      },
      set: function(newValue) {
        const _uft8 = Web3.utils.hexToUtf8(newValue);
        this.v = _uft8;
      }
    },
  },
  methods: {
    enter(e) {

    }
  },
  created() {}
};
</script>

